'use client';

import Link from "next/link";
import { usePathname } from "next/navigation";
import { useState } from "react";

import { Button } from "@/components/ui/button";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  Sheet,
  SheetContent,
  SheetTrigger,
} from "@/components/ui/sheet";
import { ThemeToggle } from "@/components/site/theme-toggle";
import { cn } from "@/lib/utils";
import {
  personaOptions,
  PersonaLabel,
  useDemoStore,
} from "../_state/demoStore";
import type { DemoRole } from "../_data/mockData";

const navItems = [
  { href: "/demo", label: "Overview", icon: "🏠" },
  { href: "/demo/leagues", label: "Leagues", icon: "🏆" },
  { href: "/demo/schools", label: "Schools", icon: "🏫" },
  { href: "/demo/teams", label: "Teams", icon: "🛡️" },
  { href: "/demo/venues", label: "Venues", icon: "📍" },
  { href: "/demo/events", label: "Events", icon: "📅" },
  { href: "/demo/officials", label: "Officials", icon: "🕹️" },
  { href: "/demo/requests", label: "Requests", icon: "📨" },
  { href: "/demo/approvals", label: "Approvals", icon: "✅" },
  { href: "/demo/assignments", label: "Assignments", icon: "📝" },
  { href: "/demo/announcements", label: "Announcements", icon: "📣" },
  { href: "/demo/users", label: "Users & Roles", icon: "🧑‍🤝‍🧑" },
  { href: "/demo/feature-flags", label: "Feature Flags", icon: "🚩" },
  { href: "/demo/settings", label: "Settings", icon: "⚙️" },
  { href: "/demo/analytics", label: "Analytics", icon: "📊" },
  { href: "/demo/activity", label: "Activity", icon: "📡" },
  { href: "/demo/api-explorer", label: "API Explorer", icon: "🛰️" },
  { href: "/demo/dev-tools", label: "Dev Tools", icon: "🧰" },
];

export default function DemoShell({ children }: { children: React.ReactNode }) {
  const pathname = usePathname();
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);
  const currentPersona = useDemoStore((state) => state.currentPersona);
  const currentRole = useDemoStore((state) => state.currentRole);
  const setPersona = useDemoStore((state) => state.setPersona);
  const branding = useDemoStore((state) => state.branding);

  const renderNavLinks = () => (
    <nav className="mt-6 flex flex-col gap-1">
      {navItems.map((item) => {
        const isActive =
          item.href === "/demo"
            ? pathname === "/demo"
            : pathname.startsWith(item.href);

        return (
          <Link
            key={item.href}
            href={item.href}
            onClick={() => setIsSidebarOpen(false)}
            className={cn(
              "flex items-center gap-3 rounded-xl px-4 py-2 text-sm transition focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-[hsl(var(--accent))]",
              isActive
                ? "bg-[hsl(var(--accent)/0.15)] text-[hsl(var(--accent))]"
                : "text-muted-foreground hover:bg-[hsl(var(--accent)/0.08)] hover:text-foreground",
            )}
          >
            <span aria-hidden="true">{item.icon}</span>
            {item.label}
          </Link>
        );
      })}
    </nav>
  );

  return (
    <div className="flex min-h-screen w-full bg-background">
      <aside className="hidden w-64 flex-col border-r border-border bg-background/80 px-4 py-8 lg:flex">
        <div className="flex items-center gap-3 px-2">
          <div className="flex h-9 w-9 items-center justify-center rounded-full bg-[hsl(var(--accent))]/20 text-lg">
            {branding.logoDataUrl ? (
              // eslint-disable-next-line @next/next/no-img-element
              <img
                src={branding.logoDataUrl}
                alt="Custom logo"
                className="h-9 w-9 rounded-full object-cover"
              />
            ) : (
              "🏀"
            )}
          </div>
          <div>
            <p className="text-sm font-semibold text-foreground">SportsHub Demo</p>
            <p className="text-xs text-muted-foreground">Mock mode active</p>
          </div>
        </div>
        {renderNavLinks()}
      </aside>

      <div className="flex flex-1 flex-col">
        <header className="flex items-center justify-between border-b border-border bg-background/80 px-4 py-4 backdrop-blur">
          <div className="flex items-center gap-3 lg:hidden">
            <Sheet open={isSidebarOpen} onOpenChange={setIsSidebarOpen}>
              <SheetTrigger asChild>
                <Button
                  variant="ghost"
                  size="icon"
                  className="border border-border"
                  aria-label="Open navigation"
                >
                  ☰
                </Button>
              </SheetTrigger>
              <SheetContent side="left" className="w-64 bg-background">
                <div className="mb-6 flex items-center gap-3 px-2">
                  <div className="flex h-9 w-9 items-center justify-center rounded-full bg-[hsl(var(--accent))]/20 text-lg">
                    {branding.logoDataUrl ? (
                      // eslint-disable-next-line @next/next/no-img-element
                      <img
                        src={branding.logoDataUrl}
                        alt="Custom logo"
                        className="h-9 w-9 rounded-full object-cover"
                      />
                    ) : (
                      "🏀"
                    )}
                  </div>
                  <div>
                    <p className="text-sm font-semibold text-foreground">SportsHub Demo</p>
                    <p className="text-xs text-muted-foreground">Mock mode active</p>
                  </div>
                </div>
                {renderNavLinks()}
              </SheetContent>
            </Sheet>
            <Link href="/" className="text-sm font-semibold text-foreground">
              SportsHub
            </Link>
          </div>

          <div className="hidden items-center gap-3 lg:flex">
            <Link href="/" className="text-sm font-semibold text-foreground">
              SportsHub
            </Link>
            <span className="rounded-full border border-[hsl(var(--accent)/0.3)] bg-[hsl(var(--accent)/0.12)] px-3 py-1 text-xs font-semibold uppercase tracking-[0.3em] text-[hsl(var(--accent))]">
              Interactive Demo
            </span>
          </div>

          <div className="flex items-center gap-3">
            <div className="hidden items-center gap-2 md:flex">
              <PersonaSelect
                currentPersona={currentPersona}
                setPersona={setPersona}
                currentRole={currentRole}
              />
            </div>
            <ThemeToggle />
          </div>
        </header>

        <main className="flex-1 overflow-y-auto bg-background px-6 pb-16 pt-10">
          {children}
        </main>
      </div>
    </div>
  );
}

function PersonaSelect({
  currentPersona,
  setPersona,
  currentRole,
}: {
  currentPersona: PersonaLabel;
  setPersona: (persona: PersonaLabel) => void;
  currentRole: DemoRole;
}) {
  return (
    <div className="flex items-center gap-2 rounded-full border border-border bg-card/80 px-3 py-1.5">
      <span className="text-xs uppercase tracking-[0.3em] text-muted-foreground">
        Role
      </span>
      <Select value={currentPersona} onValueChange={setPersona}>
        <SelectTrigger className="w-48 border-none bg-transparent text-sm text-foreground focus:ring-0 focus:ring-offset-0">
          <SelectValue placeholder="Select role" />
        </SelectTrigger>
        <SelectContent className="bg-card text-card-foreground">
          {personaOptions.map((option) => (
            <SelectItem key={option.label} value={option.label}>
              {option.label}
            </SelectItem>
          ))}
        </SelectContent>
      </Select>
      <span className="rounded-full bg-[hsl(var(--accent)/0.15)] px-2 py-0.5 text-[10px] font-semibold uppercase tracking-wider text-[hsl(var(--accent))]">
        {currentRole}
      </span>
    </div>
  );
}
